import uniSegmentedControl from '../../components/uni-segmented-control/uni-segmented-control.vue'
import uniCard from '../../components/uni-card/uni-card.vue'
import dragButton from '../../components/drag-button/drag-button.vue'
import cmdIcon from "../../components/cmd-icon/cmd-icon.vue"
import outData from '../../data/outStore.js'
import purchaseData from '../../data/purchase.js'
import sellData from '../../data/sell.js'
import exchangeData from '../../data/exchange.js'

export default {
	components: {
		uniSegmentedControl,
		uniCard,
		dragButton,
		cmdIcon
	},
	data() {
		return {
			items: ['出库','请购','销售','转库'],
			current: 0,
			//将data文件夹中的数据读入
			outList: outData.data,
			purchaseList: purchaseData.data,
			sellList: sellData.data,
			exchangeList: exchangeData.data,
			// filterText: ''
		}
	},
	// computed:{
	// 	filterList () {
	// 		var arr = []
	// 		this.outList.forEach((item) => arr.push(item))
	// 		if (this.filterText) {
	// 			arr = this.outList.filter(item => item.mso_status.includes(this.filterText))
	// 		}
	// 		return arr
	// 	}
	// },
	methods: {
		//切换tab
		onClickItem(e) {
			if (this.current !== e.currentIndex) {
				this.current = e.currentIndex;
			}
		},
		//判断订单状态
		judgeStatus(status) {
			if (status === "草稿") {
				return 0;
			}
			else if(status === "已审批") {
				return 1;
			}
			else if(status === "已关闭") {
				return 2;
			}
		},
		//查看订单详情
		viewDetail(iden) {
			var diff = iden[0]+iden[1]
			if(diff === "MS") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "PR") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "SO") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "ST") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			}
		},
		//新增单据
		newOrder(page) {
			if(page === 0) {
				uni.navigateTo({
				    url: '../user/phoneus',
				});
			} else if(page === 1) {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(page === 2) {
				uni.navigateTo({
				    url: '../user/setting',
				});
			} else if(page === 3) {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			}
		},
		//删除单据
		deleteOrder(iden) {
			var diff = iden[0]+iden[1]
			if(diff === "MS") {
				uni.showModal({
				    title: '提示',
				    content: '确认删除草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				                url: '../user/myinfo',
				            });
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			} else if(diff === "PR") {
				uni.showModal({
				    title: '提示',
				    content: '确认删除草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
							uni.navigateTo({
								url: '../user/myinfo',
							});
				        } else if (res.cancel) {
				            
				        }
				    }
				});
			} else if(diff === "SO") {
				uni.showModal({
				    title: '提示',
				    content: '确认删除草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
							uni.navigateTo({
								url: '../user/myinfo',
							});
				        } else if (res.cancel) {
				            
				        }
				    }
				});
			} else if(diff === "ST") {
				uni.showModal({
				    title: '提示',
				    content: '确认删除草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
							uni.navigateTo({
								url: '../user/myinfo',
							});
				        } else if (res.cancel) {
				            
				        }
				    }
				});
			}
		},
		//提交草稿
		commitOrder(iden) {
			var diff = iden[0]+iden[1]
			if(diff === "MS") {
				uni.showModal({
				    title: '提示',
				    content: '确认提交草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				                url: '../user/myinfo',
				            });
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			} else if(diff === "PR") {
				uni.showModal({
				    title: '提示',
				    content: '确认提交草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
							uni.navigateTo({
								url: '../user/myinfo',
							});
				        } else if (res.cancel) {
				            
				        }
				    }
				});
			} else if(diff === "SO") {
				uni.showModal({
				    title: '提示',
				    content: '确认提交草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
							uni.navigateTo({
								url: '../user/myinfo',
							});
				        } else if (res.cancel) {
				            
				        }
				    }
				});
			} else if(diff === "ST") {
				uni.showModal({
				    title: '提示',
				    content: '确认提交草稿：'+iden+" ?",
				    success: function (res) {
				        if (res.confirm) {
							uni.navigateTo({
								url: '../user/myinfo',
							});
				        } else if (res.cancel) {
				            
				        }
				    }
				});
			}
		},
		//编辑草稿
		editOrder(iden) {
			var diff = iden[0]+iden[1]
			if(diff === "MS") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "PR") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "SO") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			} else if(diff === "ST") {
				uni.navigateTo({
				    url: '../user/myinfo',
				});
			}
		},
		//关闭订单
		closeOrder(iden) {
			uni.showModal({
			    title: '提示',
			    content: '确认关闭请购单：'+iden+" ?",
			    success: function (res) {
			        if (res.confirm) {
			            uni.navigateTo({
			                url: '../user/myinfo',
			            });
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},

	}
	
	// onLoad: function() {   //登录检查函数
